input{
    background: #91ba086e;
    border-radius: 2px;
    border: 2px solid #202329;
    margin-top: 10px;
    height: 30px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  input::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
 input:focus {
    outline-color: #317d11cc;
  }