.Context {
    margin: 24px 0 24px 24px;
    width: calc(100% - 250px);
    font-size: 25px;
    line-height: 20px;
    font-weight: 400;
    font-weight: bolder;
  }
  .Context--hecho {
    text-decoration: line-through;
  }
  
  .Icono-svg {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .Icono-check {
    position: absolute;
    left: 12px;
  }
  .Icono-check--verde {
    color: #4caf50;
  }
  
  .Icono-borrar {
    position: absolute;
    top: -24px;
    right: 0;
  }
  .Icono-borrar:hover .Icono-svg {
    fill: red;
  }
  .Icono-check:hover .Icono-svg {
    fill: green;
  }